import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import { cleanContactCart, removeContact } from "../../redux/cart/cartRedux";
import { DUMMY_USER_URL } from "../../config/appConfig";
import { mobile } from "../../responsive";
import { constLang } from "../../constant/language";
import React, { useState } from "react";
import { Remove } from "@material-ui/icons";
import { LoadingButton } from "../Core/LoadingButton";
import { createContactTransaction } from "../../redux/cart/apiCalls";
import { showSuccessMsg } from "../../services/eventBusService";


const Info = styled.div`
  flex: 3;
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

const Product = styled.div`
  display: flex;
  ${mobile({ flexDirection: "column" })}
`;

const ProductDetail = styled.div`
  flex: 2;
  display: flex;
`;

const Image = styled.img`
  width: 25%;
  min-width: 136px;
  max-width:250px;
  object-fit: fill;
  aspect-ratio: 3/2;
`;

const Details = styled.div`
display: flex;
flex-direction: column;
justify-content: center;
`;

const ProductName = styled.span`
  padding: 5px 10px;
`;

const PriceDetail = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const ProductAmountContainer = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  cursor:pointer;
`;

const Hr = styled.hr`
  background-color: #eee;
  border: none;
  height: 1px;
`;


const Summary = styled.div`
  flex: 1;
  border: 0.5px solid lightgray;
  border-radius: 10px;
  padding: 20px;
  height: 50vh;
`;

const SummaryTitle = styled.h1`
  font-weight: 200;
`;

const SummaryItem = styled.div`
  margin: 30px 0px;
  display: flex;
  justify-content: space-between;
  font-weight: ${(props) => props.type === "total" && "500"};
  font-size: ${(props) => props.type === "total" && "24px"};
`;

const SummaryItemText = styled.span``;

const SummaryItemPrice = styled.span``;

export const ContactCart = () => {
  const { contact } = useSelector((state) => state.cart)
  const [isLoading, setIsLoading] = useState(false)
  const dispatch = useDispatch()

  const onRemoveItem = (item) => {
    dispatch(removeContact(item))
  }

  const onPurchase = async () => {
    setIsLoading(true)
    try {
      await createContactTransaction(dispatch, contact)
      dispatch(cleanContactCart())
      showSuccessMsg('Purchase succeeded')
    } catch (err) {
      console.log(err)
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <>
      <Info>
        {contact.contacts.map((contact, idx) => (
          <React.Fragment key={idx}>
            <Product>
              <ProductDetail>
                <Image src={contact.img.url || DUMMY_USER_URL} />
                <Details>
                  <ProductName>
                    <b>{constLang.FIELD}:</b> {contact.desc}
                  </ProductName>
                  <ProductName>
                    <b>{constLang.COMPANY}:</b> {contact.company}
                  </ProductName>
                  <ProductName>
                    <b>{constLang.Job_Title}:</b> {contact.jobTitle}
                  </ProductName>
                  <ProductName>
                    <b>{constLang.PRICE}:</b> {contact.price}$
                  </ProductName>
                </Details>
              </ProductDetail>
              <PriceDetail>
                <ProductAmountContainer>
                  <Remove onClick={() => onRemoveItem(contact)} />
                </ProductAmountContainer>
              </PriceDetail>
            </Product>
            <Hr />
          </React.Fragment>
        ))}
      </Info>
      <Summary>
        <SummaryTitle>ORDER SUMMARY</SummaryTitle>
        <SummaryItem>
          <SummaryItemText>Subtotal</SummaryItemText>
          <SummaryItemPrice>$ {contact.total}</SummaryItemPrice>
        </SummaryItem>
        <SummaryItem>
          <SummaryItemText>Contacts Amount</SummaryItemText>
          <SummaryItemPrice>{contact.quantity}</SummaryItemPrice>
        </SummaryItem>
        <SummaryItem type="total">
          <SummaryItemText>Total</SummaryItemText>
          <SummaryItemPrice>$ {contact.total}</SummaryItemPrice>
        </SummaryItem>
        <LoadingButton isLoading={isLoading} onClick={onPurchase}>
          Checkout
        </LoadingButton>
      </Summary>
    </>
  )
}