

export const utilService = {
    scrollToTop,
    getFirstLetterUppercase,
    formatMostRecentDate,
    getLastSentence,
    getUserExpensesTotal,
}

function scrollToTop() {
    window.scrollTo(0, 0);
}

function getFirstLetterUppercase(str) {
    return str.charAt(0).toUpperCase() + str.slice(1);
}

function formatMostRecentDate(data) {
    if (!data) return 'Not updated'
    // Map the array of objects to an array of dates
    const dates = data
        .map((obj) => obj.createdAt)
        .filter((dateString) => dateString !== undefined) // Filter out any undefined values
        .map((dateString) => new Date(dateString));

    // Check if there are any dates to work with
    if (dates.length === 0) {
        return undefined; // Or whatever value you want to return if there are no valid dates
    }

    // Find the most recent date
    const mostRecentDate = new Date(Math.max.apply(null, dates));

    // Format the date as dd/m/yyyy
    const day = mostRecentDate.getDate();
    const month = mostRecentDate.getMonth() + 1; // getMonth returns 0-11, so add 1 for the actual month number
    const year = mostRecentDate.getFullYear();
    return `${day}/${month}/${year}`;
}

function getLastSentence(str) {
    var lastIndex = str.lastIndexOf("/") + 1;
    return str.substring(lastIndex).replace(/-/g, " ");
}

function getUserExpensesTotal(user) {
    if (!user.contactTransactions) return 0
    let amount = 0;
    user.contactTransactions.forEach(transaction => amount += transaction.price)
    return amount
}